.nav-button {
  background-color: #ffffff13;
  color: rgb(255, 255, 255) !important;
  margin: 1px;
  border: 0.2px solid rgba(255, 255, 255, 0.432);
  min-width: 110px;
  text-align: center;
  backdrop-filter: blur(1px);
  font-size: 15px;
  border-radius: 5px;
  transition: 0.2s;
  transition-delay: 0.1s;
  overflow: hidden;
  padding: 8px;
  &:hover {
    transform: scale(1.05);
    background-color: #55d89c;
    box-shadow: 0 0 5px #55d89c, 0 0 10px #55d89c, 0 0 15px #55d89c;
    color: black !important;
  }
}

.load-component-container {
  padding: 180px;
}

.load-component {
  display: flex;
  justify-content: center;
  align-items: center;
}

.disabled-nav {
  &:hover {
    transform: none !important;
    background-color: #ffffff13 !important;
    box-shadow: none !important;
    color: black !important;
    border: 0.2px solid rgba(0, 0, 0, 0.432);
  }
}

.nav-underline {
  gap: 0;
}

/* * Media Query */

@media (max-width: 346px) {
  .nav-button {
    min-width: 90px;
    height: 40px;
  }
  .large-button {
    font-size: 11px;
  }
}

@media (max-width: 625px) {
  .info-card {
    margin: 20px !important;
    padding: 15px !important;
  }
}

@media (max-width: 580px) {
  .photo-container {
    justify-content: center !important;
  }
}

@media (max-width: 743px) {
  .description {
    margin: 40px 0px 10px 0px !important;
  }
  .form-container {
    margin: 5px !important;
  }
}

@media (max-width: 968px) {
  .nav {
    flex-wrap: nowrap !important;
  }
}

@media (max-width: 988px) {
  .description {
    margin: 60px 10px 10px 10px;
  }
}
